<template>
    <div class="left-bar">
        <span class="fa fa-list"/>
        <div class="menu-group">
            <span class="fa fa-television active menu_span" @click="onClickActive"/>
            <span class="fa fa-folder menu_span" @click="onClickActive"/>
            <!--        <div class="building">-->
            <!--          <span class="fa fa-building"></span>-->
            <!--          <span class="fa fa-building"></span>-->
            <!--        </div>-->
            <span class="fa fa-bookmark" @click="onClickActive"/>
            <span class="fa fa-cog menu_span" @click="onClickActive"/>
            <a href="/" class="fa fa-sign-out menu_span"/>
        </div>

    </div>
</template>

<script>
export default {
  name: 'SideMenu',
  data () {
    return {
      isActive: ['fa', 'fa-television', 'active', 'menu_span'],
      isUnActive: ['fa', 'fa-television', 'menu_span']
    }
  },
  methods: {
    onClickActive (event) {
      window.document.querySelector('.active').classList.remove('active')
      event.target.classList.add('active')
    }
  }
}
</script>

<style scoped>
    .left-bar {
        position: relative;
        z-index: 5;
        width: 70px;
        height: 100%;
        background: linear-gradient(180deg, black,#171a1d, #272727);
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .left-bar span {
        padding: 5px;
    }
    .left-bar > span {
        cursor: pointer;
        -webkit-transform: scale(1, 1);
        transform: scale(1, 1);
        transition: all ease-in-out 150ms;
    }
    .left-bar > span:hover {
        -webkit-transform: scale(1.2, 1.2);
        transform: scale(1.2, 1.2);
    }
    .left-bar > span:first-child {
        margin-top: 20px;
        color: white;
        font-size: 26px;
    }
    .left-bar > span:last-child {
        color: rgba(255, 255, 255, 0.7);
        margin-top: 500px;
        font-size: 20px;
    }

    .menu-group {
        height: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        margin-top: 70px;
    }
    .menu-group > span {
        color: rgba(255, 255, 255, 0.5);
        cursor: pointer;
        -webkit-transform: scale(1);
        transform: scale(1);
        transition: all ease-in-out 150ms;
    }
    .menu-group > span:hover {
        -webkit-transform: scale(1.2, 1.2);
        transform: scale(1.2, 1.2);
    }
    .menu-group .active {
        color: white;
    }
    .menu-group .active:after {
        content: "";
        position: absolute;
        margin-left: 3px;
        margin-top: -4px;
        padding: 4px;
        background-color: yellow;
        border-radius: 50%;
        box-shadow: 0 0 5px 2px rgba(255, 255, 0, 0.5);
    }
</style>
